// import $ from 'jquery'
import Vue from 'vue'
import VueMaterial from 'vue-material'
import 'vue-material/dist/components/mdCore/index.css'
import 'vue-material/dist/components/mdCard/index.css'
import 'vue-material/dist/components/mdIcon/index.css'
import 'vue-material/dist/components/mdButton/index.css'
import 'vue-material/dist/components/mdToolbar/index.css'
// import 'bootstrap/dist/css/bootstrap.css'

Vue.use(VueMaterial.MdCore)
Vue.use(VueMaterial.MdCard)
Vue.use(VueMaterial.MdIcon)
Vue.use(VueMaterial.MdButton)
Vue.use(VueMaterial.MdToolbar)

import VueDragZone from 'components/vue-drag-zone'
import VueCodemirror from 'components/vue-codemirror'
// import VueQuillEditor from 'vue-quill-editor'
import VueQuillEditor from 'components/vue-quill-editor'
import VueTouchRipple from 'components/vue-touch-ripple'
import VueVideoPlayer from 'vue-video-player'
import VueAwesomeSwiper from 'vue-awesome-swiper'

VueVideoPlayer.config({
  youtube: true,
  switcher: true,
  hls: true
})

Vue.use(VueDragZone)
Vue.use(VueCodemirror)
Vue.use(VueQuillEditor)
Vue.use(VueTouchRipple)
Vue.use(VueVideoPlayer)
Vue.use(VueAwesomeSwiper)

console.log(Vue.material)

Vue.material.registerTheme('blue', {
  primary: 'blue',
  accent: 'pink'
})

Vue.material.setCurrentTheme('blue')

import App from 'projects/vue-awesome-swiper/App.vue'
// import App from 'projects/vue-touch-ripple/App.vue'
// import App from 'projects/vue-video-player/App.vue'
// import App from 'projects/vue-quill-editor/App.vue'
// import App from 'projects/vue-wang-editor/App.vue'
// import App from 'projects/vue-codemirror/App.vue'
// import App from 'projects/vue-touch-ripple/App.vue'
// import App from 'projects/vue-drag-zone/App.vue'

import 'sass/app.scss'

// ---------------------------------------------------------------------

// 配置路由
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const routes = [{
  name: 'index',
  path: '/',
  component: App
}]

const router = new VueRouter({
  routes,
  mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    return { y: 0 }
  }
})

const app = new Vue({
  router,
  render: h => h(App)
}).$mount('app')

// ---------------------------------------------------------------------

